body{background: #fbfbfb;}
header.transparent{display:block!important;background:transparent!important;box-shadow: none!important;}
header.stickbottom{background:#fff!important;box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)!important;}
#root,html  {
    height: 100%;
    background:#f6f6f6;
  }
  a,button:focus,a:focus{outline:none!important;text-decoration: none;}
.right-nav{    flex: 1;
    display: flex;
    justify-content: flex-end;align-items:center;}
.right-nav>a{color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    padding: 0.4rem .7rem;}

header.stickbottom .right-nav>a{color:#333!important;}
header.transparent .right-nav>a:hover{color:#20bee8;text-decoration: none;}
.expert-speak-container {
    background: url(/assets/car_clean_bg.jpg) no-repeat,linear-gradient(180deg,#0c6284 50%,#ffffff 101%);
    min-height: 400px;
    background-blend-mode: soft-light;
    background-size: 100%;
    background-position: top;
    }
.pt-70{padding-top: 70px;}
.expert-say-container{
    display: flex;}
.expert-say-container .expert-thumbs img{    width: 160px;
    background: white;
    margin-right: 20px;}
.fsize-20,.designation{font-size: 20px;}
.expert-carousel{padding:4rem 0rem 10rem 0rem;}
.expert-carousel .carousel-indicators{bottom:80px;}
/* .expert-carousel .carousel-indicators .active{background-color: #0d6eff;} */
.expert-carousel .carousel-control-next {
    right: -20%;
}
.expert-carousel .carousel-control-prev {
    left: -20%;
}
.ruler-hr{width: 40px;
    border-top: 2px solid #ccc;
    margin: 23px auto;transition-duration: 500ms;}
.ruler-hr:hover{width:80px;transition-duration: 500ms;color:#333;}
.footer-nav a{    color: #8993A4;
    text-decoration: underline;margin:0 0.6rem;}
.footer-nav a:hover{color:#333;}
.service-container{  display:flex;  flex-direction: row;
    flex-wrap: nowrap;
    /* max-width: 830px; */
    justify-content: space-between;
    margin: 0 auto;}
.service-container .pricing-card{flex: 1 ;

    margin: 10px;
    
    border-radius: 4px;
    
    overflow: hidden;
    
    background: white;
    
    box-shadow: 0px 0px 11px rgb(210, 209, 209);}
.top-img-title img{width:100%;}
.top-img-title h3{background: #f3f6ff;

    color: #577bc9;
    
    padding: 20px 0px;
    
    margin: 0px;
    
    text-transform: uppercase;
    
    font-size: 21px;}

.pricing-card .pricing{

    text-align: center;
font-size: 32px;
font-weight: 600;
    
    padding: 6px 0px;}
.pricing-card .duration{font-size: 17px;

    font-weight: 100;}
.pricing-card .features p{margin: 0px;

    /* padding: 10px 20px 10px 60px;
    
   
    
    color: #646464;
    
    font-size: 18px;} */
    padding: 10px 6px 10px 14px;
color: #646464;
font-size: 17px;

}
    .pricing-card .features p:nth-child(odd) { background: #f7f7f7;}
    .custom-primary{background:#0d6eff!important;
        color: white!important;}
    .copyright{text-align: center;
        color: #939191;}
    p#standard-basic-helper-text{margin:0px;}
    .gap-to-header{margin-top:5rem;}
    .open>.dropdown-menu {
        display: block;
        position: absolute;
        right:0!important;
        left:auto!important;
        min-width: 250px!important;
        padding:20px;
      }
    .account-dropdown:after{border:none!important;margin:0;padding:0;display:none}
    .account-dropdown{width:40px;height:40px;border-radius:50%;text-align:center;cursor:pointer;line-height:40px;background:#ccc;display:block;}
    .MuiSelect-root{padding:4px!important;}
    .profile-pic{width: 150px;
        height: 150px;
        border-radius: 50%;
        margin: 0 auto;}
    .grid-stats h2{font-size: 1.1rem;

        display: flex;
        
        justify-content: space-between;
        
        align-items: center;
        
        padding:15px 11px;
        
        box-shadow: 0px 0px 3px #e1e0e0;
        
        border-radius: 4px;margin-bottom:8px;}
    .MuiTablePagination-select{text-align-last: left!important;

        min-width: 35px!important;}

        .plans-type-groups .btn-default{padding: 2rem 0px;
            background: #f3f6ff;
            color: hsl(226, 21%, 61%);border-bottom:1px solid #fff;}
        .plans-type-groups .active{background:#3f51b5!important;color:white!important;}
        .right-nav a:hover{color:#fff!important;outline:none;text-decoration: none;}
        .MuiExpansionPanelSummary-content.Mui-expanded>p{    color: #3f51b5;
            font-weight: bold;
        }
        .MuiGrid-spacing-xs-8{margin:0px!important;width:100%!important}
        .MuiTableCell-head{font-weight:600!important;background:#f0f0f0;}

        .MuiGrid-spacing-xs-8 > .MuiGrid-item {
            padding: 20px 5px!important;
        }

        .custom-input-label{transform: translate(14px, -6px) scale(0.75)!important;
            background: white!important;
            z-index: 1!important;
            padding: 0px 8px!important;}
            .react-datepicker-wrapper{width:100%;}
        .custom-input-01{    width: 100%;
            height: 40px;
            border-radius: 4px;
            padding:5px;
            border: 1px solid #ccc;margin-top:1rem;}
           
            .MuiDialog-paperWidthSm{    overflow-y:initial!important;}
            .h56{max-height:56px!important;height:56px!important;}
            .react-datepicker-popper{z-index: 2;}
            .activity-image{opacity: 0.3;
                margin: 40px auto;
                width: 150px;
                display: block;}
            .is-active{color: #ff9edd!important;}